.universal-call-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  font-size: 12px;
  .universal-call-form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 991px;
    .custom-width {
      width: 150px;
    }
    .radio-btn {
      display: flex;
      align-items: center;
      &.missed-call{
        font-size: 15px;
      }
      label{
        margin-left: 5px;
      }
    }
    .button {
      padding: 10px 0px;
      width: 150px;
    }
  }
  .react-table-container {
    width: 100%;
    overflow-x: scroll;
    .warning-note {
      margin-left: 20px;
      padding-bottom: 20px;
    }
    .ReactTable {
      .rt-table {
        .rt-tbody {
          .rt-tr-group {
            .rt-tr {
              .rt-td:last-child {
                // width: 250px !important;
                .audio-player {
                  width: 280px;
                  height: 35px;
                }
              }
            }
          }
        }
      }
    }
  }
}

.create-member-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  .change-password-form {
    margin: 20px 0px;
    font-size: 12px;
    background: white;
    padding: 30px;
    box-shadow: 0px 0px 5px #efefef;
    max-width: 450px;
    width: 100%;
    .password-container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .button {
        width: 150px;
        padding: 10px 0px;
        margin-bottom: 15px;
        margin-left: 20px;
      }
    }
    .form-control-custom {
      padding-bottom: 10px;
    }
    .number-container {
      display: flex;
      align-items: center;
      input {
        text-align: center;
      }
      .plus-symbol {
        padding: 12px;
        color: #666;
      }
    }
    .button-container {
      float: right;
      padding-top: 10px;
      button {
        width: 200px;
      }
    }
  }
}
